/*#root {margin-top: -10%;}*/
body {
  background: url(./img/971.png) no-repeat;
  background-size: 70%;
  background-position: right 0%;
  background-color: #130469;
  margin: auto;
}

ul {
  float: right;
  width: 100%;
}
ul li {
  float: right;
  display: inline-block;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  color: aliceblue;
  margin-right: 5%;
}
a {
  text-decoration: none;
}

article {
  position: relative;
  width: 40%;
  margin-top: 10%;
  margin-left: 5%;
}
article h1 {
  font-family: cursive;
  color: aliceblue;
  font-weight: 700;
  font-size: 50px;
}
article p {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: beige;
  width: 50%;
}
#c_gb {
  font-weight: 700;
  color: gold;
}
article section button {
  border: 1px solid #fff;
  border-radius: 15px;
  width: 150px;
  height: 30px;
  background: none;
  color: floralwhite;
  cursor: pointer;
  transition: 0.4s;
  margin-top: 5%;
}
article section button:hover {padding-left: 10px; width: 250px; color: gold; border: 1px solid  gold;}
.fixtop {
  margin-top: -10%;
}

#about {
  width: 28%;
  text-align: justify;
  color: aliceblue;

  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  margin-top: 0%;
  margin-left: 1%;
}